:root {
    --bs-primary: #ffa200 !important;
    --bs-secondary: #000 !important;
    --bs-secondary-rgb: 0, 0, 0 !important;
    --bs-body-color: #fff !important;
    --bs-danger: #f00 !important
}

.btn-primary {
    background-color: var(--bs-primary) !important;
    border-color: var(--bs-primary) !important;
}

:root .text-primary {
    color: var(--bs-primary) !important;
}

.btn-danger {
    background-color: var(--bs-danger) !important;
}

.btn-primary:focus {
    box-shadow: 0 0 0 0.25rem rgba(255, 162, 0, 0.5) !important;
}

.btn-outline-primary {
    color: var(--bs-primary) !important;
    border-color: var(--bs-primary) !important;
}

.card {
    background-color: var(--bs-secondary) !important;
    border-color: var(--bs-primary) !important;
}

.modal-content {
    background-color: var(--bs-secondary) !important;
    box-shadow: 1px 2px 10px 0px rgba(255, 162, 0, 0.75);
    -webkit-box-shadow: 1px 2px 10px 0px rgba(255, 162, 0, 0.75);
    -moz-box-shadow: 1px 2px 10px 0px rgba(255, 162, 0, 0.75);
}

.form-control {
    color: var(--bs-body-color) !important;
    border-color: var(--bs-primary) !important;
    background-color: transparent !important;
}

.navbar {
    box-shadow: 1px 2px 10px 0px rgba(255, 162, 0, 0.75);
    -webkit-box-shadow: 1px 2px 10px 0px rgba(255, 162, 0, 0.75);
    -moz-box-shadow: 1px 2px 10px 0px rgba(255, 162, 0, 0.75);
}